






















import Vue from 'vue';
import {Component, Prop} from "vue-property-decorator";

@Component
export default class NumberPad extends Vue{
  @Prop(Number) readonly defaultAmount!: number;

  output = this.defaultAmount.toString();
  inputNumber(event: MouseEvent){ //event
    const button = event.target as HTMLButtonElement; //event.target.textContent
    const input = button.textContent!;  // !
    if (this.output.length === 16) { return; }
    if (this.output === '0') {
      if ('0123456789'.indexOf(input) >= 0) {
        this.output = input;
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf('.') >= 0 && input === '.') {return;}
    this.output += input;
  }
  remove() {
    if (this.output.length === 1) {
      this.output = '0';
    } else {
      this.output = this.output.slice(0, -1);
    }
  }
  clear() {
    this.output = '0';
  }
  finish(){
    if (this.output === '0'){
      window.alert(' !');
    } else {
      this.$emit('update:defaultAmount', parseFloat(this.output));
      this.$emit('submit');
      this.output = '0';
      window.alert(' !')
    }
  }
}
